.StartButton {
    grid-area: 1 / 3 / 2 / 5;
    border: none;
    border-radius: 5px;
    background-color: #FFCC03;
    font-family: 'Luckiest Guy', cursive;
    text-transform: uppercase;
    font-size: calc(8px + 2vmin);
    color: #3C59A5;

    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 8px;
    padding-left: 5%;
    padding-right: 5%;
    min-width: 5rem;
    margin-top: 10%;
    height: 8vh;
}

