.Wrapper {
    height: 100vh;
    width: 100vw;
    background-image: url(../Wrapper/../../assets/background1.png);
    background-size: cover;
    background-position: center;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(12, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
}

